.modalWrapper{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0, 0.5 );
    display: none;
}

.modalContent{
    padding: 25px;
    background: white;
    border-radius: 16px;
    min-width: 250px
}

.modalWrapper.modalActive{
    display: flex;
    justify-content: center;
    align-items: center;
}